.addButton {
    width: 170px;
    height: 40px;
    color: #f0dcdc;
    font-family: Woodstick;
    font-size: 20px;
    text-transform: uppercase;
    background: transparent;
    cursor: pointer;
    transition: all 0.3s ease;
    position: relative;
    display: inline-block;
    box-shadow:inset 2px 2px 2px 0px rgba(255,255,255,.5),
    7px 7px 20px 0px rgba(0,0,0,.1),
    4px 4px 5px 0px rgba(0,0,0,.1);
    outline: none;
    background: rgb(0,172,238);
    background: linear-gradient(0deg, rgba(0,172,238,1) 0%, rgba(2,126,251,1) 100%);
    line-height: 42px;
    padding: 0;
    border: none;
}

.addButton span {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
}

.addButton:before,
.addButton:after {
    position: absolute;
    content: "";
    right: 0;
    top: 0;
    background: rgba(2,126,251,1);
    transition: all 0.3s ease;
}

.addButton:before {
    height: 0%;
    width: 2px;
}

.addButton:after {
    width: 0%;
    height: 2px;
}

.addButton:hover {
    background: transparent;
    box-shadow: none;
}

.addButton:hover:before {
    height: 100%;
}

.addButton:hover:after {
    width: 100%;
}

.addButton span:hover {
    color: rgba(2,126,251,1);
}

.addButton span:before,
.addButton span:after {
    position: absolute;
    content: "";
    left: 0;
    bottom: 0;
    background: rgba(2,126,251,1);
    transition: all 0.3s ease;
}

.addButton span:before {
    width: 2px;
    height: 0%;
}

.addButton span:after {
    width: 0%;
    height: 2px;
}

.addButton span:hover:before {
    height: 100%;
}

.addButton span:hover:after {
    width: 100%;
}